<template>
  <div>
    <div class="wrapper">
      <div class="box">
        <div class="IDbox" ref="IDbox">
          <div class="rotebox" ref="boxs">
            <img class="rote" id="imgs" src="../../assets/images/ktp-overlay.png" />
          </div>
          <video v-show="imgSrc!=null" id="videoCamera" class="canvas" autoPlay></video>
          <canvas v-if="ratio" id="canvasCamera" class="canvas" :width="width*ratio" :height="height*ratio"></canvas>
        </div>
      </div>
      <div class="buttons" v-show="!hasphoto">
        <div>
          <van-button type="primary" round @click="drawImage">
            Take
          </van-button>
        </div>
        <div>
          {{checked?'Front':'Back'}}
          <van-switch @change="change" v-model="checked" />
        </div>
      </div>

      <div class="buttons" v-show="hasphoto">
        <div>
          <van-button type="primary" round @click="yes">
            OK
          </van-button>
        </div>
        <div>
          <van-button type="danger" round @click="resetCanvas">
            reset
          </van-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "XXX",
  data() {
    return {
      ratio: 10,
      imgSrc: null,
      hasphoto: false,
      checked: false,
      visible: false, //弹窗
      loading: false, //上传按钮加载
      os: false, //控制摄像头开关
      thisVideo: null,
      thisContext: null,
      thisCancas: null,
      videoWidth: 500,
      videoHeight: 400,
      postOptions: [],
      CertCtl: "",
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 总条数
      total: 0,
      // 项目人员表格数据
      akworkerList: [],
      //工种类别数据字典
      workerTypeOptions: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      mode: "environment",
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        imgSrc: undefined
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {},
      height: 0,
      width: 0
    };
  },
  created() {
    this.getCompetence();
    this.getImageSize("../../assets/images/ktp-overlay.png");
  },
  methods: {
    change() {
      this.mode = this.mode === "user" ? "environment" : "user";
      this.getCompetence();
    },
    getImageSize(url) {
      console.log(url);
      return new Promise(resolve => {
        var img = document.createElement("img");
        img.src = url;
        console.log(img);
        // img.onload = () => {
        setTimeout(() => {
          let dom = document.getElementById("imgs");
          console.log(dom);
          this.height = dom.clientHeight;
          this.width = dom.clientWidth;
          console.log(this.height, this.width);
          this.$refs.boxs.style.width = this.width + "px";
          this.$refs.boxs.style.height = this.height + "px";
          this.$refs.IDbox.style.width = this.width + "px";
          this.$refs.IDbox.style.height = this.height + "px";
        }, 200);
        // 为什么要写 onload  是因为要等他加载完之后才能获取到图片宽高
        resolve(img.naturalWidth, img.naturalHeight); //  2064,4608
        // };
      });
    },
    openclick() {
      this.visible = true;
      this.getCompetence();
    },
    /*调用摄像头拍照开始*/
    onTake() {
      this.visible = true;
      this.getCompetence();
    },
    onCancel() {
      this.visible = false;
      /* this.resetCanvas();*/
      this.stopNavigator();
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;

      return (window.devicePixelRatio || 1) / backingStore;
    },
    // 调用摄像头权限
    getCompetence() {
      //必须在model中render后才可获取到dom节点,直接获取无法获取到model中的dom节点
      this.$nextTick(() => {
        const _this = this;
        this.os = false; //切换成关闭摄像头
        this.thisCancas = document.getElementById("canvasCamera");
        this.thisContext = this.thisCancas.getContext("2d");
        this.thisVideo = document.getElementById("videoCamera");
        this.ratio = this.getPixelRatio(this.thisContext);
        // 旧版本浏览器可能根本不支持mediaDevices，我们首先设置一个空对象
        if (navigator.mediaDevices === undefined) {
          console.log(navigator.mediaDevices);
          navigator.menavigatordiaDevices = {};
        }
        // 一些浏览器实现了部分mediaDevices，我们不能只分配一个对象
        // 使用getUserMedia，因为它会覆盖现有的属性。
        // 这里，如果缺少getUserMedia属性，就添加它。
        if (navigator.mediaDevices.getUserMedia === undefined) {
          navigator.mediaDevices.getUserMedia = function(constraints) {
            // 首先获取现存的getUserMedia(如果存在)
            let getUserMedia =
              navigator.webkitGetUserMedia ||
              navigator.mozGetUserMedia ||
              navigator.getUserMedia;
            // 有些浏览器不支持，会返回错误信息
            // 保持接口一致
            if (!getUserMedia) {
              return Promise.reject(
                new Error("getUserMedia is not implemented in this browser")
              );
            }
            // 否则，使用Promise将调用包装到旧的navigator.getUserMedia
            return new Promise(function(resolve, reject) {
              getUserMedia.call(navigator, constraints, resolve, reject);
            });
          };
        }
        const constraints = {
          // audio: false,
          video: {
            // width: _this.width,
            // height: _this.height,
            // transform: "scaleX(-1)",
            facingMode: this.mode
          }
        };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function(stream) {
            // 旧的浏览器可能没有srcObject
            if ("srcObject" in _this.thisVideo) {
              _this.thisVideo.srcObject = stream;
            } else {
              // 避免在新的浏览器中使用它，因为它正在被弃用。
              _this.thisVideo.src = window.URL.createObjectURL(stream);
            }
            _this.thisVideo.onloadedmetadata = function(e) {
              _this.thisVideo.play();
            };
          })
          .catch(err => {
            this.$notify({
              title: "警告",
              message: "没有开启摄像头权限或浏览器版本不兼容.",
              type: "warning"
            });
          });
      });
    },
    //绘制图片
    drawImage() {
      // 点击，canvas画图
      this.thisContext.drawImage(
        this.thisVideo,
        0,
        0,
        this.width * this.ratio + 50,
        this.height * this.ratio
      );
      // 获取图片base64链接
      this.imgSrc = this.thisCancas.toDataURL("image/png");
      this.hasphoto = true;
      this.stopNavigator();
      /*const imgSrc=this.imgSrc;*/
    },
    yes() {
      sessionStorage.setItem("imgSrc", this.imgSrc);
      this.$router.go(-1);
    },
    //清空画布
    clearCanvas(id) {
      let c = document.getElementById(id);
      let cxt = c.getContext("2d");
      cxt.clearRect(0, 0, c.width, c.height);
    },
    //重置画布
    resetCanvas() {
      this.imgSrc = null;
      this.clearCanvas("canvasCamera");
      this.hasphoto = false;
      this.getCompetence();
    },
    //关闭摄像头
    stopNavigator() {
      if (this.thisVideo && this.thisVideo !== null) {
        this.thisVideo.srcObject.getTracks()[0].stop();
        this.os = true; //切换成打开摄像头
      }
    }
    /*调用摄像头拍照结束*/
  }
};
</script>

<style>
.box {
  overflow: hidden;
}
.buttons {
  width: 60%;
  overflow: hidden;
  margin: 0 auto;
  margin-top: 10px;
}
.buttons > div:nth-child(1) {
  float: left;
}
.buttons > div:nth-child(2) {
  float: right;
}
.rotebox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.rote {
  width: 100%;
  display: block;
  float: left;
  transform: scale(0.8);
}
.IDbox {
  /* border: solid 1px red; */
  margin: 0 auto;
  width: 90%;
}
#videoCamera {
  width: 100%;
  height: 100% !important;
  display: block !important;
  /* border: solid 1px blue !important; */
}
#canvasCamera {
  width: 100% !important;
  height: 100% !important;
  padding: 0 !important;
  margin: 0 !important;
  /* border: solid 3px blue; */
}
#canvasCamera {
  /* border: solid 3px yellow; */
  position: absolute;
  left: 0;
  top: 0;
  /* opacity: 0.7; */
  /* transform: scale(0.8); */
  transform: scale(0.8);
}
.IDbox {
  position: relative;
  /* transform: rotate(90deg); */
}
.IDimg {
  width: 80%;
  height: 200px;
  margin: 0 auto;
  border: solid 1px #cccccc;
}
* {
  box-sizing: border-box;
}
video:focus {
  outline: -webkit-focus-ring-color auto 0px;
}
</style>



